import kotlinx.css.Color
import kotlinx.css.backgroundColor
import kotlinx.css.color
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import react.dom.h1
import react.dom.h2
import styled.css
import styled.styledDiv

external interface KuralPorulProps: RProps {
    var selectedThirukkural: Thirukkural
    var showAnswer: Boolean
}

class KuralPorul : RComponent<KuralPorulProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            h1 {
                +props.selectedThirukkural.porul
            }
        }
        if (props.showAnswer) {
            styledDiv {
                css {
                    backgroundColor = Color.azure
                    color = Color.blue
                }
                h2 {
                    +props.selectedThirukkural.kural.firstLine
                }
                h2 {
                    +props.selectedThirukkural.kural.secondLine
                }
            }
        }
    }
}

fun RBuilder.kuralPorul(handler: KuralPorulProps.() -> Unit): ReactElement {
    return child(KuralPorul::class) {
        this.attrs(handler)
    }
}
