import kotlinx.coroutines.MainScope
import kotlinx.coroutines.launch
import kotlinx.css.Align
import kotlinx.css.Color
import kotlinx.css.Position
import kotlinx.css.alignContent
import kotlinx.css.backgroundColor
import kotlinx.css.fontSize
import kotlinx.css.padding
import kotlinx.css.position
import kotlinx.css.px
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import react.setState
import styled.css
import styled.styledButton
import styled.styledDiv
import kotlin.random.Random

external interface TopicPros: RProps {
    var selectedTopic: Topic
    var showAnswer: Boolean
    var thirukkurals: List<Thirukkural>
    var athikarams: List<String>
    var onShowAnswerClick: (Boolean) -> Unit
}

external interface TopicState: RState {
    var loaded: Boolean
    var athikaramIndex: Int
    var kuralPorulIndex: Int
}

class TopicContent: RComponent<TopicPros, TopicState>() {
    override fun TopicState.init() {
        val mainScope = MainScope()
        mainScope.launch {
            setState {
                athikaramIndex = Random.nextInt(0, props.athikarams.size)
                kuralPorulIndex = Random.nextInt(0, props.thirukkurals.size)
                loaded = true
            }
        }
    }

//    override fun componentDidMount() {
//        println("componentDidMount...")
//        super.componentDidMount()
//    }
//
//    override fun componentDidUpdate(prevProps: TopicPros, prevState: TopicState, snapshot: Any) {
//        println("componentDidUpdate...")
//        super.componentDidUpdate(prevProps, prevState, snapshot)
//    }

//    override fun componentWillMount() {
//        super.componentWillMount()
//        println("componentWillMount...")
//    }

    override fun componentWillUnmount() {
        println("componentWillUnmount...")
        super.componentWillUnmount()
    }

    override fun RBuilder.render() {
        if (state.loaded) {
            styledDiv {
                css {
                    position = Position.relative
                    alignContent = Align.flexStart
                    padding(vertical = 20.px)
                }
                styledButton {
                    css {
                        backgroundColor = Color.lightCyan
                        fontSize = 20.px
                    }
                    +""
                    attrs {
                        onClickFunction = {
                            props.onShowAnswerClick(true)
                        }
                    }
                }
                styledButton {
                    css {
                        backgroundColor = Color.lightCyan
                        fontSize = 20.px
                    }
                    +""
                    attrs {
                        onClickFunction = {
                            props.onShowAnswerClick(false)
                            when (props.selectedTopic) {
                                Topic.Athikaram, Topic.AthikaramPorul -> {
                                    setState {
                                        athikaramIndex = getNextRandomInt(athikaramIndex, props.athikarams.size)
                                    }
                                }
                                Topic.KuralPorul -> {
                                    setState {
                                        kuralPorulIndex = getNextRandomInt(kuralPorulIndex, props.thirukkurals.size)
                                    }
                                }
                            }
                        }
                    }
                }
            }
            if (props.selectedTopic == Topic.Athikaram) {
                athikaram {
                    selectedThirukkurals = props.thirukkurals
                        .filter { it.athikaram == props.athikarams[state.athikaramIndex] }
                    showAnswer = props.showAnswer
                }
            }
            if (props.selectedTopic == Topic.AthikaramPorul) {
                athikaramPorul {
                    selectedThirukkurals = props.thirukkurals
                        .filter { it.athikaram == props.athikarams[state.athikaramIndex] }
                    showAnswer = props.showAnswer
                }
            }
            if (props.selectedTopic == Topic.KuralPorul) {
                kuralPorul {
                    selectedThirukkural = props.thirukkurals[state.kuralPorulIndex]
                    showAnswer = props.showAnswer
                }
            }
        }
    }

    private fun getNextRandomInt(currentIndex: Int, maxIndex: Int): Int {
        var newIndex: Int
        do {
            newIndex = Random.nextInt(0, maxIndex)
        } while (newIndex == currentIndex)
        println("Current: $currentIndex to New: $newIndex of Total: $maxIndex")
        return newIndex
    }
}

fun RBuilder.topicContent(handler: TopicPros.() -> Unit): ReactElement {
    return child(TopicContent::class) {
        this.attrs(handler)
    }
}
