import kotlinx.css.Color
import kotlinx.css.backgroundColor
import kotlinx.css.color
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import react.dom.h1
import react.dom.h2
import styled.css
import styled.styledDiv

external interface AthikaramProps: RProps {
    var selectedThirukkurals: List<Thirukkural>
    var showAnswer: Boolean
}

class Athikaram : RComponent<AthikaramProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            h1 {
                +props.selectedThirukkurals[0].athikaram
            }
        }
        if (props.showAnswer) {
            props.selectedThirukkurals.forEach { thirukkural ->
                styledDiv {
                    css {
                        backgroundColor = Color.azure
                        color = Color.blue
                    }
                    h2 {
                        +thirukkural.kural.firstLine
                    }
                    h2 {
                        +thirukkural.kural.secondLine
                    }
                }
            }
        }
    }
}

fun RBuilder.athikaram(handler: AthikaramProps.() -> Unit): ReactElement {
    return child(Athikaram::class) {
        this.attrs(handler)
    }
}
